<template>
  <div class="toggle-crew">
    <p class="content-title"></p>
    <div class="select-content">
      <p v-if="selectCrewInfo.ID">
        : &nbsp;&nbsp;{{ selectCrewInfo.DisplayName }}
        <span :class="`crew-status crew-status-${selectCrewInfo.Status}`">
          {{ showByCrewStatus(selectCrewInfo.Status).text }}
          <!-- crewStatusList[parseInt(selectCrewInfo.Status)]  -->
        </span>
      </p>
      <p v-else>: -</p>
      <div class="toggle-search-box">
        <div class="search-item">
          <span>:</span>
          <el-input placeholder="" v-model="crewCode" @input="crewCodeInput">
          </el-input>
        </div>
        <div class="search-item">
          <span>:</span>
          <el-select
            v-model="crewStatus"
            placeholder=""
            @change="crewStatusChange"
            :popper-append-to-body="false"
          >
            <el-option
              v-for="item in statusList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span>:</span>
          <position-cascader placeholder="" @positionChange="positionChange" />
          <!-- <el-cascader
            v-model="selectedOptions"
            placeholder=""
            :options="options"
            @change="positionChange"></el-cascader> -->
        </div>
        <div class="search-item">
          <span>:</span>
          <el-select
            v-model="crewId"
            placeholder=""
            :popper-append-to-body="false"
            v-lazy-load="lazyOption"
          >
            <el-option
              v-for="item in searchData"
              :key="item.ID"
              :label="item.DisplayName"
              :value="item.ID"
            >
            </el-option>
          </el-select>
        </div>
        <el-button type="primary" class="change-btn" @click="toggleCrew()"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import PositionCascader from '@/components/PositionCascader'
import { showByCrewStatus } from '@/utils/common'

export default {
  name: 'toggle-crew',
  inject: ['reload'], // App
  components: {
    PositionCascader
  },
  data() {
    return {
      showByCrewStatus,
      crewStatusList: ['', '', '', '', ''], // 0 1 2 3 4
      statusList: [
        { value: 0, label: '' },
        { value: 2, label: '' },
        { value: 3, label: '' },
        { value: 4, label: '' },
        { value: 5, label: '' },
        { value: 6, label: '' }
      ], // 0  2 3 4 5  6
      selectCrewInfo: {}, // 
      crewList: [], // list
      crewCode: '', // 
      crewStatus: 0, // 
      crewPosition: '', // 
      crewId: '', // id

      // +
      searchData: [],
      curPage: 1,
      pageSize: 20,
      lazyOption: {
        loadData: this.loadData,
        distance: 20,
        scrollBody: '.el-scrollbar__wrap', // el-select DOMclass
        changeCurPage: this.changeCurPage,
        callback: (fn) => {
          // , .
          this.$once('hook:beforeDestroy', () => fn())
        }
      }
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  mounted() {
    //
    this.selectCrewInfo = this.selectCrew
    this.getCrewList()
  },
  methods: {
    ...mapActions(['toggleSelectCrew']),
    getCrewList() {
      const query = {
        // IsWeb: true,
        page: 0,
        pageSize: 999
      }
      const params = {
        model: {
          DisplayMode: 1, // 10 2
          IsLease: 1, // 
          UserID: -1,
          CollectorToken: this.crewCode, // 
          StatusMode: this.crewStatus, // 0  2 3 4 5  6
          InstallationSite: this.crewPosition
        }
      }
      this.$api.common.getCrewList(params, query).then((res) => {
        if (res.Code == 1) {
          this.crewList = res.Data.Data
          this.loadData()
          if (
            res.Data &&
            res.Data.Data &&
            res.Data.Data.length > 0 &&
            !(this.selectCrewInfo && this.selectCrewInfo.ID)
          ) {
            this.selectCrewInfo = res.Data.Data[0]
            this.toggleSelectCrew(this.selectCrewInfo)
          }
        }
      })
    },
    loadData() {
      // console.log('loadData', this.crewList, this.lazyOption)
      this.searchData = this.crewList.slice(0, this.pageSize * this.curPage)
      // console.log(this.searchData)
    },
    changeCurPage() {
      this.curPage++
    },
    crewCodeInput(value) {
      // this.crewCode = value
      this.crewId = ''
      this.getCrewList()
    },
    crewStatusChange(value) {
      this.crewId = ''
      this.getCrewList()
    },
    positionChange(data) {
      this.crewPosition = data.crewPosition
      this.crewId = ''
      this.crewList = []
      this.getCrewList()
    },
    toggleCrew() {
      if (this.crewId) {
        const selectInfo = this.crewList.filter((item) => item.ID === this.crewId)[0]
        this.toggleSelectCrew(selectInfo)
        return
      }
      this.$message({
        type: 'error',
        message: ''
      })
    }
  },
  watch: {
    // 
    selectCrew(newVal, oldVal) {
      //
      this.selectCrewInfo = newVal
    }
  }
}
</script>

<style lang="less" scoped>
.crew-status-0 {
  color: #808080;
}
.crew-status-1 {
  color: #2e2d28;
}
.crew-status-2 {
  color: #ffd800;
}
.crew-status-3 {
  color: red;
}
.crew-status-4 {
  color: #5cf548;
}
.toggle-crew {
  margin-bottom: 16px;
  .select-content {
    padding: 0 16px;
    > p {
      margin-bottom: 16px;
    }
    .crew-status {
      font-weight: 700;
    }
    .toggle-search-box {
      display: flex;
      margin-bottom: 16px;
      .search-item {
        width: 25%;
        margin-right: 24px;
        display: flex;
        align-items: center;
        > span {
          white-space: nowrap; /*span*/
          margin-right: 8px;
        }
      }
    }
    .change-btn {
      height: 32px;
      // margin-top: 4px;
    }
  }
}
</style>



