/**
 * @auth Demon3443002624@qq.com
 * @desc global color configuration
 */

// 0  1 () 2 () 3  4 
const crewStatus = ['#20be62', '#d9001b', '#f59a23', '#fff', '#808080']
// 0  1  2  3  4 
const rentingStatus = ['#c082fb', '#3be9b8', '#eefd35', '#e87e8f', '#28a9ec']

export default {
  // ***  *** //
  crewStatus: crewStatus,
  crewStatusMapping: {
    stop: crewStatus[3],
    error: crewStatus[1],
    offline: crewStatus[4],
    warning: crewStatus[2],
    running: crewStatus[0]
  },
  // ***  *** //
  rentingStatus: rentingStatus,
  rentingStatusMapping: {
    check: rentingStatus[0],
    renting: rentingStatus[1],
    construct: rentingStatus[2],
    idle: rentingStatus[3],
    repair: rentingStatus[4]
  }
}



