// 
const StrategyTypeEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' }
]
// 
const eventTypeEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' }
]
// 
const eventStatusEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' }
]
export default {
  /********  ********/
  crewManageColumns: (that) => [
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'EngineModel',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'RatedPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'RunHourTotal',
      minWidth: '15%',
      unit: 'h'
    },
    {
      label: '',
      prop: 'InstallationSite',
      minWidth: '25%',
      render: (h, parmas) => {
        return h(
          'span',
          {
            class: 'text-overflow-ellipsis MulLineHiding', //
            attrs: {
              title: parmas.row.InstallationSite || ''
            }
          },
          parmas.row.InstallationSite
        )
      }
    },
    {
      label: '',
      prop: 'IsParallel',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.IsParallel ? '' : ''
      }
    },
    {
      label: '',
      prop: 'isOpen',
      minWidth: '18%',
      render: (h, parmas) => {
        return [
          h('div', {}, [
            h('el-switch', {
              props: {
                value: parmas.row.IsFenceOpen,
                disabled: that.projectStatus === 3
              },
              on: {
                input: (value) => {
                  that.setElectronicFenceSwitch(parmas.row, value)
                }
              }
            }),
            h('br')
            // h(
            //   'span',
            //   { display: 'block' },
            //   parmas.row.IsFenceOpen ? `r=${parmas.row.IsFenceOpen}` : ''
            // )
          ])
        ]
      }
    },
    {
      label: '',
      prop: 'IsStrategy',
      minWidth: '18%',
      formatter: (row, column, index) => {
        return row.IsStrategy ? '' : ''
      },
      render: (h, parmas) => {
        return [
          h('div', {}, [
            h('el-switch', {
              props: {
                value: parmas.row.IsStrategy,
                disabled: that.projectStatus === 3
              },
              on: {
                input: (value) => {
                  that.setStrategySwitch(parmas.row, value)
                }
              }
            })
          ])
        ]
      }
    }
  ],

  /********  ********/
  StrategyTypeEnum,
  // 
  strategyColumns: (that) => [
    {
      label: '',
      prop: 'StrategyName',
      minWidth: '40%'
    },
    {
      label: '',
      prop: 'EngineBrandName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'EngineModelName',
      minWidth: '20%'
      // formatter: (row, column, index) => {
      //   return row.Type ? StrategyTypeEnum.find((item) => item.value == row.Type).label : ''
      // }
    },
    {
      label: '',
      prop: 'Type',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Type ? StrategyTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    }
  ],
  //  
  listOperates: (that) => [
    {
      label: '',
      method: (row) => {
        that.handleSelect(row)
      },
      setStyle: (row) => {
        if (that.selectItem && row.StrategyId == that.selectItem.StrategyId) return 'color: red;'
      },

      disabled: (row) => {
        return !row.IsUsing
      }
    }
  ],

  /********  ********/
  eventTypeEnum,
  eventStatusEnum,

  projectProgressColumns: (that) => [
    {
      label: '',
      prop: 'Type',
      minWidth: '10%',
      formatter: (row, column, index) => {
        return row.Type ? eventTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '',
      prop: 'ProgressName',
      minWidth: '20%'
    },
    // {
    //   label: '',
    //   prop: 'type',
    //   minWidth: '10%'
    //   // formatter: (row, column, index) => {
    //   //   return row.FileType
    //   //     ? ProjectFileTypeEnum.find((item) => item.value == row.FileType).label
    //   //     : ''
    //   // }
    // },
    // {
    //   label: '',
    //   prop: 'type',
    //   minWidth: '10%'
    // },
    {
      label: '',
      prop: 'LastCreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount > 0
          ? row.LastCreateTime
            ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
            : ''
          : '-'
      }
    },
    {
      label: '',
      prop: 'LastCreateUserName',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.LastCreateUserName || '-'
      }
    },
    {
      label: '',
      prop: 'RecordCount',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.RecordCount || 0
      }
    }
  ],
  // 
  projectProgressRecordsColumns: (that) => [
    {
      label: '',
      prop: 'Type',
      minWidth: '10%',
      formatter: (row, column, index) => {
        return row.Type ? eventTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '',
      prop: 'ProgressName',
      minWidth: '20%'
    },
    // {
    //   label: '',
    //   prop: 'type',
    //   minWidth: '25%'
    // },
    {
      label: '',
      prop: 'GensetCount',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.IsGenset ? row.GensetCount : '-'
      }
    },
    {
      label: '',
      prop: 'CreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '',
      prop: 'CreateUserName',
      minWidth: '20%'
    }
  ],
  // 
  progressRecordsColumns: (that) => [
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'Type',
      minWidth: '10%',
      formatter: (row, column, index) => {
        return row.Type ? eventTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '',
      prop: 'ProgressName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'RecordCount',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount || 0
      }
    }
    // {
    //   label: '',
    //   prop: 'type',
    //   minWidth: '25%'
    // }
  ],
  // 
  GensetRalatedColumns: (that) => [
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '25%'
      // formatter: (row, column, index) => {
      //   return 'GHha-lksl-fjkl'
      // }
    },
    {
      label: '',
      prop: 'EngineModel',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'RatedPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'RunHourTotal',
      minWidth: '15%',
      unit: 'h'
    },
    {
      label: '',
      prop: 'limitPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'IsParallel',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.IsParallel ? '' : ''
      }
    }
  ],

  /********  ********/
  crewInfoColumns: (that) => [
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '25%'
      // formatter: (row, column, index) => {
      //   return 'GHha-lksl-fjkl'
      // }
    },
    {
      label: '',
      prop: 'EngineModel',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'RatedPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'RunHourTotal',
      minWidth: '15%',
      unit: 'h'
    },
    {
      label: '',
      prop: 'InstallationSite',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'limitPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'IsParallel',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.IsParallel ? '' : ''
      }
    },
    {
      label: '',
      prop: 'isOpen',
      minWidth: '18%',
      formatter: (row, column, index) => {
        return row.IsStrategy ? '' : ''
      }
    },
    {
      label: '',
      prop: 'IsStrategy',
      minWidth: '18%',
      formatter: (row, column, index) => {
        return row.IsStrategy ? '' : ''
      }
    }
  ],
  gensetProgressColumns: (that) => [
    {
      label: '',
      prop: 'Type',
      minWidth: '10%',
      formatter: (row, column, index) => {
        return row.Type ? eventTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '',
      prop: 'ProgressName',
      minWidth: '20%'
    },
    // {
    //   label: '',
    //   prop: 'type',
    //   minWidth: '10%'
    //   // formatter: (row, column, index) => {
    //   //   return row.FileType
    //   //     ? ProjectFileTypeEnum.find((item) => item.value == row.FileType).label
    //   //     : ''
    //   // }
    // },
    {
      label: '',
      prop: 'LastCreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount > 0
          ? row.LastCreateTime
            ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
            : ''
          : '-'
      }
    },
    {
      label: '',
      prop: 'LastCreateUserName',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.LastCreateUserName || '-'
      }
    },
    {
      label: '',
      prop: 'RecordCount',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.RecordCount || 0
      }
    }
  ],
  gensetProgressRecordsColumns: (that) => [
    {
      label: '',
      prop: 'Type',
      minWidth: '10%',
      formatter: (row, column, index) => {
        return row.Type ? eventTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '',
      prop: 'ProgressName',
      minWidth: '20%'
    },
    // {
    //   label: '',
    //   prop: 'type',
    //   minWidth: '25%'
    // },
    {
      label: '',
      prop: 'CreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '',
      prop: 'CreateUserName',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.CreateUserName || '-'
      }
    }
  ]
}



