// 
const ProjectTypeEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' }
]
// 
const ProjectStatusEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' },
  { value: 3, label: '' }
]
// 
const ProjectFileTypeEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' },
  { value: 3, label: '' }
]

export default {
  /********  ********/
  ProjectTypeEnum,
  ProjectStatusEnum,
  // 
  listColumns: (that) => [
    {
      label: '',
      prop: 'ProjectName',
      minWidth: '35%'
    },
    {
      label: '',
      prop: 'ProjectType',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.ProjectType
          ? ProjectTypeEnum.find((item) => item.value == row.ProjectType).label
          : ''
      }
    },
    {
      label: '',
      prop: 'ProjectNumber',
      minWidth: '35%'
    },
    {
      label: '',
      prop: 'TotalPower',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'Quantity',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'ProjectArea',
      minWidth: '40%'
    },
    {
      label: '',
      prop: 'CreateTime',
      minWidth: '30%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '',
      prop: 'Status',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Status ? ProjectStatusEnum.find((item) => item.value == row.Status).label : ''
      }
    },
    {
      label: '',
      prop: 'StartDate',
      minWidth: '30%',
      formatter: (row, column, index) => {
        return row.StartDate ? that.$moment(row.StartDate).format('YYYY.MM.DD') : ''
      }
    },
    {
      label: '',
      prop: 'EndDate',
      minWidth: '30%',
      formatter: (row, column, index) => {
        return row.EndDate ? that.$moment(row.EndDate).format('YYYY.MM.DD') : ''
      }
    },
    {
      label: '',
      prop: 'EndDateLast',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.EndDateLast || ''
      }
    }
  ],

  /********  ********/
  ProjectFileTypeEnum,
  /*    */
  contractColumns: (that) => [
    {
      label: '',
      prop: 'FileType',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.FileType
          ? ProjectFileTypeEnum.find((item) => item.value == row.FileType).label
          : ''
      }
    },
    {
      label: '',
      prop: 'FileName',
      minWidth: '35%'
    },
    {
      label: '',
      prop: 'CreateTime',
      minWidth: '35%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '',
      prop: 'CreateUserName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'FileDetail',
      minWidth: '25%'
    }
  ],
  /*    */
  fileListColumns: (that) => [
    {
      label: '',
      prop: 'FileName',
      minWidth: '75%'
    },
    {
      label: '',
      prop: 'FileSize',
      minWidth: '25%'
    }
  ],

  /*    */
  personnelColumns: (that) => [
    {
      label: '',
      prop: 'CustomerType',
      minWidth: '35%',
      formatter: (row, column, index) => {
        return row.CustomerType
          ? that.CustomerTypeEnum.find((item) => item.value == row.CustomerType).label
          : ''
      }
    },
    {
      label: '',
      prop: 'CompanyName',
      minWidth: '25%'
    },
    // {
    //   label: '',
    //   prop: 'num',
    //   minWidth: '35%'
    // },
    // {
    //   label: '',
    //   prop: 'UserType',
    //   minWidth: '25%',
    //   formatter: (row, column, index) => {
    //     return row.UserType
    //       ? that.UserTypeEnum.find((item) => item.value == row.UserType).label
    //       : ''
    //   }
    // },
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'Phone',
      minWidth: '25%'
    }
  ],

  /*    */
  crewColumns: (that) => [
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'EngineModel',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'RatedPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'RunHourTotal',
      minWidth: '15%',
      unit: 'h'
    },
    {
      label: '',
      prop: 'InstallationSite',
      minWidth: '25%',
      render: (h, parmas) => {
        return h(
          'span',
          {
            class: 'text-overflow-ellipsis MulLineHiding', //
            attrs: {
              title: parmas.row.InstallationSite || ''
            }
          },
          parmas.row.InstallationSite
        )
      }
    },
    {
      label: '',
      prop: 'limitPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'IsParallel',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.IsParallel ? '' : ''
      }
    },
    // {
    //   label: '',
    //   prop: 'isOpen',
    //   minWidth: '18%',
    //   render: (h, parmas) => {
    //     return [
    //       h('div', {}, [
    //         h('el-switch', {
    //           props: {
    //             value: parmas.row.IsFenceOpen,
    //             disabled: that.projectStatus === 3
    //           },
    //           on: {
    //             input: (value) => {
    //               that.setElectronicFenceSwitch(parmas.row, value)
    //             }
    //           }
    //         }),
    //         h('br')
    //         // h(
    //         //   'span',
    //         //   { display: 'block' },
    //         //   parmas.row.IsFenceOpen ? `r=${parmas.row.IsFenceOpen}` : ''
    //         // )
    //       ])
    //     ]
    //   }
    // },
    // {
    //   label: '',
    //   prop: 'IsStrategy',
    //   minWidth: '18%',
    //   formatter: (row, column, index) => {
    //     return row.IsStrategy ? '' : ''
    //   },
    //   render: (h, parmas) => {
    //     return [
    //       h('div', {}, [
    //         h('el-switch', {
    //           props: {
    //             value: parmas.row.IsStrategy,
    //             disabled: that.projectStatus === 3
    //           },
    //           on: {
    //             input: (value) => {
    //               that.setStrategySwitch(parmas.row, value)
    //             }
    //           }
    //         })
    //       ])
    //     ]
    //   }
    // }
  ],
  selectCrewColumns: (that) => [
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    // {
    //   label: '',
    //   prop: 'CollectorToken',
    //   minWidth: '20%'
    // },
    {
      label: '',
      prop: 'EngineModel',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'RatedPower',
      minWidth: '20%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'RunHourTotal',
      minWidth: '20%',
      unit: 'h'
    },
    {
      label: '',
      prop: 'InstallationSite',
      minWidth: '25%',
      render: (h, parmas) => {
        return h(
          'span',
          {
            class: 'text-overflow-ellipsis MulLineHiding', //
            attrs: {
              title: parmas.row.InstallationSite || ''
            }
          },
          parmas.row.InstallationSite
        )
      }
    }
  ],
  /*    */
  parallelColumns: (that) => [
    {
      label: '',
      prop: 'ParallelJoinGensetIsMain',
      minWidth: '20%%'
    },
    {
      label: '',
      prop: 'ParallelJoinGensetIsNotMain',
      minWidth: '50%'
    },
    // {
    //   label: '',
    //   prop: 'ParallelJoinGensetIsMain',
    //   minWidth: '20%%'
    // },
    // {
    //   label: '',
    //   prop: 'ParallelJoinGensetIsNotMain',
    //   minWidth: '50%'
    // },
    {
      label: '',
      prop: 'GensetTotal',
      minWidth: '10%'
    },
    {
      label: '',
      prop: 'num',
      minWidth: '20%',
      unit: 'Kw'
    }
  ],
  setParallelCrewColumns: (that) => [
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    // {
    //   label: '',
    //   prop: 'CollectorToken',
    //   minWidth: '25%'
    // },
    {
      label: '',
      prop: 'EngineModel',
      minWidth: '35%'
    },
    {
      label: '',
      prop: 'RatedPower',
      minWidth: '20%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'RunHourTotal',
      minWidth: '20%',
      unit: 'h'
    }
  ]
}



